	export default {
		data() {
			return {
				form:{
					messageTitle:''	,//标题
					messageDetails:	'' //	内容

				},
				
			};
		},
		computed:{
		   i18n() {  
		     return this.$i18nMsg()  
		   }
		},
		onNavigationBarButtonTap:function(val){
			 
			this.tapSettingButton() 
		    
		},
		onLoad:function(options){
             uni.setNavigationBarTitle({
                         title:this.$i18nMsg().user.contactUs
                     });
			var currentWebview = this.$mp.page.$getAppWebview();  
			currentWebview.setTitleNViewButtonStyle(0, {  
				text: this.$i18nMsg().user.moreMessage, 
			});  
		},
		methods:{
            tapSettingButton:function(){
            	uni.navigateTo({
            		url: './messageList'
            	
            	});
            },
			async submit(){
				
				const res = await this.$http.get('/message/add',{data: this.form})
				console.log(res)
				uni.showToast({
					title: '提交成功',
					icon: 'none'
				});
				setTimeout(function() {
					uni.navigateBack({
						delta: 1
					});
				}, 1500);
				
				
			}
		}
	}
